




















































import { ClosetModule, Dress } from "@/store/modules/closet";
import { Component, Vue } from "vue-property-decorator";
import VueButtonAnimation from 'vue-button-animation'

Vue.use(VueButtonAnimation);

@Component
export default class Create extends Vue {
    step = 0;
    dress = {
        image: "",
        name: "",
        type: "",
        size: "",
        material: "",
        situation: [],
        minTemp: 0,
        maxTemp: 0,
    } as Dress;

    typeList = ["상의", "하의", "한벌", "기타"];
    sizeList: { [key in string]: string[] } = {
        상의: ["긴팔", "반팔", "민소매", "기타"],
        하의: ["긴바지", "반바지", "롱스커트", "미니스커트", "기타"],
        한벌: ["롱원피스", "숏원피스", "긴팔 정장", "반팔 정장", "기타"],
        기타: [],
    };
    materialList = ["면", "실크", "울", "기타"];
    situationList = ["일상", "결혼식", "캠퍼스", "데이트", "오피스", "면접"];

    inputImageFile(e: InputEvent) {
        let target = e.target as HTMLInputElement;
        let file: File = target.files![0];
        this.dress.image = URL.createObjectURL(file);
    }

    selectType(type: string) {
        this.dress.type = type;
        this.step++;
    }

    selectSize(size: string) {
        this.dress.size = size;
        this.step++;
    }

    selectMaterial(material: string) {
        this.dress.material = material;
        this.step++;
    }

    appendSituation(situation: string) {
        let idx = this.dress.situation.indexOf(situation);
        if (idx == -1) this.dress.situation.push(situation);
        else this.dress.situation.splice(idx, 1);
    }
    selectSituation() {
        this.step++;
    }

    appendDress() {
        ClosetModule.appendDress(this.dress);
        this.$router.push("playground");
    }
}
