import { getModule, Module, Mutation, VuexModule } from "vuex-module-decorators";

import store from "@/store";

export interface Dress {
	image: string; // 옷의 이미지
	name: string; // 옷의 이름
	type: string; // 옷의 종류
	size: string; // 옷의 길이
	material: string; // 소재
	situation: string[]; // 상황
	minTemp: number; // 최저 온도
	maxTemp: number; // 최고 온도
}
export interface IClosetState {
	dressList: Dress[];
}

@Module({ dynamic: true, store, namespaced: true, name: "Closet", preserveState: localStorage.getItem("vuex") !== null })
class Closet extends VuexModule implements IClosetState {
	dressList: Dress[] = [];

	@Mutation
	appendDress(dress: Dress) {
		this.dressList.push(dress);
	}

	@Mutation
	resetDressList() {
		this.dressList = [];
	}
}

export const ClosetModule = getModule(Closet, store);
