











































































import { Component, Vue } from "vue-property-decorator";
import { ClosetModule } from "@/store/modules/closet";
import VueButtonAnimation from 'vue-button-animation'

Vue.use(VueButtonAnimation);

@Component
export default class Playground extends Vue {
    tags: string[] = ["일상", "결혼식", "캠퍼스", "데이트", "오피스", "면접"];
    selectedSituation: string[] = [];
    search: string = "";

    clickTag(tag: string) {
        let idx = this.selectedSituation.indexOf(tag);
        if (idx == -1) this.selectedSituation.push(tag);
        else this.selectedSituation.splice(idx, 1);
    }

    async created() {
        // let result = (await axios.get("https://dev-free-cors.herokuapp.com/http://www.kma.go.kr/wid/queryDFSRSS.jsp?zone=1117053000")).data;
        // console.log(result);
    }

    reset() {
        ClosetModule.resetDressList();
    }

    get getSearchResult() {
        if (!this.search && !this.selectedSituation.length) return this.getDressList;
        return this.getDressList.filter((dress) => {
            let result = false;
            let stringList = [dress.name, dress.type, dress.size, dress.material];
            if (this.search) stringList.forEach((str: string) => str.indexOf(this.search) != -1 && (result = true));
            if (this.selectedSituation.length) dress.situation.forEach((situation) => this.selectedSituation.indexOf(situation) != -1 && (result = true));
            return result;
        });
    }
    get getDressList() {
        return ClosetModule.dressList;
    }
}
